n@mixin transform($args) {\n  -webkit-transform: $args;\n  -ms-transform: $args;\n  transform: $args;\n}\n\n@mixin tooltip {\n  display: block;\n  background: white;\n  color: black !important;\n  border: 1px solid black;\n  font-family: monospace;\n  position: absolute;\n  font-size: 12px;\n  font-weight: bold;\n  line-height: 100%;\n  height: 15px;\n  top: -15px;\n}\n\n@mixin highlight-area($selector, $color) {\n  #{$selector} {\n    border: $border-width solid $color;\n    &:hover {\n      background: rgba(lighten($color, 30%), 0.2);\n    }\n  }\n}\n\n"],"sourceRoot":""}]);
